@import url(https://fonts.googleapis.com/css?family=Open+Sans&display=swap);
.menu-item{min-width:35%;max-width:100%;flex:1 1 center;display:flex;align-items:center;justify-content:center;border:1px solid #9fa09c;box-shadow:1px 1px 5px #f0eed5;border-radius:5px;margin:100px 15px 10px 15px;overflow:hidden;background-image:-webkit-gradient(linear, left top, right bottom, from(#000), to(#000));background-image:linear-gradient(to right bottom, #000, #000)}.menu-item:hover{cursor:pointer;box-shadow:1px 1px 15px}.menu-item:hover .background-image{-webkit-transform:scale(1.1);transform:scale(1.1);-webkit-transition:3s;transition:3s;opacity:1}.menu-item:hover .content{opacity:1;background:#fdfbf6;background:-webkit-gradient(linear, right top, left top, from(#929191), to(wheat));background:linear-gradient(to left, #929191, wheat);font-weight:bolder}.menu-item:hover .title{opacity:1;font-weight:bolder}.menu-item:hover .subtitle{opacity:1;font-weight:bolder}.menu-item .background-image{width:100%;height:100%;background-position:center;background-size:cover;-webkit-transition-duration:3s;transition-duration:3s;opacity:.6}.menu-item.large{height:450px;width:400px}.menu-item:first-child{margin-right:7.5px}.menu-item:last-child{margin-left:7.5px}.menu-item .content{height:100px;padding:0 25px;margin-top:150px;display:flex;flex-direction:column;align-items:center;justify-content:center;border:1px solid #4d4b4b;background-color:#fdfbf6;position:absolute;opacity:.8;background-image:-webkit-gradient(linear, left top, right bottom, from(rgba(0,0,0,0)), to(rgba(212,200,200,0.9)));background-image:linear-gradient(to right bottom, rgba(0,0,0,0), rgba(212,200,200,0.9))}.menu-item .content .title{font-weight:bold;margin-bottom:10px;font-size:30px;color:#000000;opacity:0.8}.menu-item .content .subtitle{font-weight:500;font-size:16px;opacity:.8}

.directory-menu{width:100%;display:flex;flex-wrap:wrap;justify-content:center}

.custom-button{min-width:165px;width:auto;height:50px;letter-spacing:0.5px;line-height:50px;padding:0 35px 0 35px;font-size:15px;background-color:black;color:white;text-transform:uppercase;font-family:'Open Sans Condensed';font-weight:bolder;border:none;cursor:pointer;display:flex;justify-content:center}.custom-button:hover{background-color:white;color:black;border:1px solid black}.custom-button.google-sign-in{background-color:#4285f4;color:white}.custom-button.google-sign-in:hover{background-color:#357ae8;border:none}.custom-button.inverted{background-color:white;color:black;border:1px solid black}.custom-button.inverted:hover{background-color:black;color:white;border:none}

.collection-item{display:flex;flex-direction:column;height:350px;align-items:center;justify-content:center;align-content:center;flex:1 1 auto;max-width:300px;margin:10px 5px 30px 5px;position:relative;width:40vw}.collection-item .image{min-width:100%;height:100%;background-size:cover;background-position:center;border-radius:5px;margin:1px}.collection-item .custom-button{width:70%;opacity:0.7;position:absolute;top:235px;display:none}.collection-item:hover .image{opacity:0.8}.collection-item:hover .custom-button{opacity:0.85;display:flex}.collection-item .collection-footer{text-align:center;width:100%;height:5%;margin-top:10px;margin-bottom:10px;font-size:18px}.collection-item .collection-footer .name{width:90%;margin-bottom:15px}.collection-item .collection-footer .price{width:10%}

.collection-preview{display:flex;flex-wrap:wrap;flex-direction:row;margin-bottom:30px;margin-top:0 0px;min-width:35%}.title{font-size:28px;margin-bottom:25px;margin:0 auto;padding-left:25px}.preview{display:flex;justify-content:center;flex-direction:row;flex-wrap:wrap}

.collections-overview{display:flex;flex-direction:column}

.collection-page{display:flex;flex-direction:column}.collection-page .title{font-size:38px;margin:0 auto 30px}.collection-page .items{display:grid;grid-template-columns:1fr 1fr 1fr 1fr;grid-gap:10px}.collection-page .items .collection-item{margin-bottom:30px}

.header{height:70px;width:100%;display:flex;justify-content:space-between;margin-bottom:25px;padding-right:30px;top:0;width:100%}.header .logo-container{height:100%;width:70px;padding-left:0px;margin-left:10px;padding-top:10px}.header .options{width:75%;height:100%;display:flex;align-items:center;justify-content:flex-end}.header .options .option{padding:10px 10px;cursor:pointer}

.cart-icon{width:45px;height:45px;position:relative;display:flex;align-items:center;justify-content:center;cursor:pointer}.cart-icon .shopping-icon{width:44px;height:24px}.cart-icon .item-count{position:absolute;font-size:10px;font-weight:bold;bottom:12px}

.group{position:relative;margin:45px 0}.group .form-input{background:none;background-color:white;color:grey;font-size:18px;padding:10px 10px 10px 5px;display:block;width:100%;border:none;border-radius:0;border-bottom:1px solid grey;margin:25px 0}.group .form-input:focus{outline:none}.group .form-input:focus ~ .form-input-label{top:-14px;font-size:12px;color:#000}.group input[type='password']{letter-spacing:0.3em}.group .form-input-label{color:grey;font-size:16px;font-weight:normal;position:absolute;pointer-events:none;left:5px;top:10px;-webkit-transition:300ms ease all;transition:300ms ease all}.group .form-input-label.shrink{top:-14px;font-size:12px;color:#000}

.sign-in{width:380px;display:flex;flex-direction:column}.title{margin:40px 0}.buttons{display:flex;justify-content:space-between}

.sign-in-and-sign-out{width:850px;display:flex;justify-content:space-between;margin:0 auto}

.sign-up{display:flex;flex-direction:column;width:380px}.sign-up .title{margin:10px 0}

.checkout-item{width:100%;display:flex;min-height:100px;border-bottom:1px solid darkgrey;padding:15px 0;font-size:20px;align-items:center}.checkout-item .image-container{width:23%;padding-right:15px}.checkout-item .image-container img{width:100%;height:100%}.checkout-item .name,.checkout-item .quantity,.checkout-item .price{width:23%}.checkout-item .quantity{display:flex}.checkout-item .arrow{cursor:pointer}.checkout-item .value{margin:0 10px}.checkout-item .remove-button{padding-left:12px;cursor:pointer}

.checkout-page{width:55%;min-height:90vh;display:flex;flex-direction:column;align-items:center;margin:50px auto 0}.checkout-page .title{font-size:30px}.checkout-page .checkout-header{width:100%;padding:10px 0;display:flex;justify-content:space-between;border-bottom:1px solid darkgrey}.checkout-page .checkout-header .header-block{text-transform:capitalize;width:23%}.checkout-page .checkout-header .header-block:last-child{width:8%}.checkout-page .total{margin-top:30px;margin-left:auto;font-size:20px}.checkout-page .test-warning{text-align:left;margin-top:20px;margin-bottom:20px;font-size:15px;color:#4b4b50;line-height:1.5;box-shadow:1px 1px 3px grey;padding:10px;margin-left:auto}.checkout-page .btn-pay-now{margin-left:auto;margin-top:20px;text-align:center}.note{margin-bottom:7.5px}

